import axios from "axios";

//获取评分关系矩阵
const getScoreMatrixApi = (pageNum, pageSize, name) => new Promise((resolve, reject) =>{
    let data = {
        name:name,
        pageNum: pageNum,
        pageSize: pageSize
    };
    axios({
        method:'post',
        url:'/api/find/relationships',
        headers:{
            "Content-Type": "application/json",
        },
        data: JSON.stringify(data),
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

//获取相关人信息
const getRelatedPersonInfoApi = (userId) => new Promise((resolve, reject) =>{
    axios({
        method:'get',
        url:'/api/matrix/relatedPersonInfo',
        headers:{
            "Content-Type": "application/json",
        },
        params: {
            userId: userId
        }
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

// 添加固定评估人(多选)
const getAddEvaluatorApi = (evaluatorIds, userId) => new Promise((resolve, reject) =>{
    let data = {
        evaluatorIds: evaluatorIds,  //示例数据:[172, 174, 169]
        userId: userId
    }
    axios({
        method:'post',
        url:'/api/add/relationship',
        headers:{
            "Content-Type": "application/json",
        },
        data: JSON.stringify(data),
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

// 删除固定评估人
const getDelEvaluatorApi = (evaluatorId, userId) => new Promise((resolve, reject) =>{
    axios({
        method:'delete',
        url:'/api/update/relationship',
        headers:{
            "Content-Type": "application/json",
        },
        params: {
            evaluatorId: evaluatorId,
            userId: userId
        },
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

// 上传矩阵关系文件
const getMatrixExcelApi = (file) => new Promise((resolve, reject) => {
    // 创建 FormData 对象
    let formData = new FormData();
    
    // 将文件添加到 FormData 对象
    formData.append('file', file);

    // 发起 POST 请求
    axios({
        method: 'post',
        url: '/api/import/relationship',
        headers: {
            'Content-Type': 'multipart/form-data',
        },
        data: formData,
    })
    .then((response) => {
        // 请求成功的处理
        resolve(response);
    })
    .catch((error) => {
        // 请求失败的处理
        reject(error.response);
    });
});

// 发布矩阵关系
const getPushRelationApi = () => new Promise((resolve, reject) =>{
    axios({
        method:'get',
        url:'/api/relation/step',
        headers:{
            "Content-Type": "application/json",
        }
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

// 导出当前矩阵关系表
const getMatrixRelationExcel = () => new Promise((resolve, reject) =>{
    axios({
        method:'get',
        url:'/api/export/relationship',
        headers:{
            "Content-Type": "application/json",
        },
        responseType: 'blob'  // 设置响应类型为 blob，表示二进制文件
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

export{
    getScoreMatrixApi,
    getAddEvaluatorApi,
    getDelEvaluatorApi,
    getMatrixExcelApi,
    getPushRelationApi,
    getMatrixRelationExcel,
    getRelatedPersonInfoApi
}