<!-- 管理员/超级管理员--维护评分矩阵 -->
<script setup>
import { reactive, ref, onMounted, onUnmounted } from "vue";
import { message } from "ant-design-vue";
import dayjs from "dayjs";
import {
  getScoreMatrixApi,
  getAddEvaluatorApi,
  getDelEvaluatorApi,
  getMatrixExcelApi,
  getPushRelationApi,
  getMatrixRelationExcel,
  getRelatedPersonInfoApi
} from "@/api/scoringMatrix";
import { getUserListApi } from "@/api/todoList";
import { getStepTimeApi } from "@/api/scoreTimeSet";
import { notification } from 'ant-design-vue'

// 获取当前评估进度
const published = ref(false);
const currentSession = ref();
// 获得当前评估进度
const getStepTime = () => {
  getStepTimeApi()
    .then((res) => {
      if (res.data.code === 200) {
        for (let i = 0; i <= res.data.data.evaluateStep.length - 1; i++) {
          if (res.data.data.evaluateStep[i].enable === 1) {
            currentSession.value = i + 1 + "";
            if (currentSession.value === "1") {
              published.value = false;
            } else {
              published.value = true;
            }
          }
        }
      } else {
        message.error("获取失败！" + res.data.msg);
      }
    })
    .catch((err) => {
      console.log("获取失败！", err);
    });
};

// 搜索框表单数据
const serchInfo = reactive({
  name: null,
});

// 当前选择的被评估人id
const evaluatorId = ref(null);
// 定义搜索功能提交函数
const searchSubmit = () => {
  pagination.current = 1; // 每次搜索将当前页数变为1
  // 调用接口进行搜索
  getScoreMatrix();
};

// 分页配置
const pagination = reactive({
  // 表格页码相关设置
  pageSize: 12, //每页最多条目数
  current: 1, //当前页码
  showSizeChanger: false,
  total: 0, //总条目数
  showTotal: (total, range) => `总共 ${total} 条数据`, // 在这里定义显示总数据量的格式
});

// 表格的列配置，更新为所需的字段
const columns = ref([
  {
    title: "序号",
    dataIndex: "index",
    key: "index",
    fixed: "left", // 固定在左侧
    align: "center",
    width: 65,
  },
  {
    title: "被评估人",
    dataIndex: "name",
    key: "name",
    fixed: "left", // 固定在左侧
    align: "center",
    width: 120,
  },
  {
    title: "部门",
    dataIndex: "department",
    key: "department",
    align: "center",
    width: 100,
  },
  {
    title: 'LXYZ类型',
    dataIndex: 'lxyz',
    key: 'lxyz',
    align: 'center',
    width: 100,
  },
  {
    title: '业务类型',
    dataIndex: 'businessType',
    key: 'businessType',
    align: 'center',
    width: 90,
  },
  {
    title: "主管1",
    dataIndex: "supervisorName1",
    key: "supervisorName1",
    align: "center",
    width: 100,
  },
  {
    title: "权重1",
    dataIndex: "weight1",
    key: "weight1",
    align: "center",
    width: 75,
  },
  {
    title: "主管2",
    dataIndex: "supervisorName2",
    key: "supervisorName2",
    align: "center",
    width: 100,
  },
  {
    title: "权重2",
    dataIndex: "weight2",
    key: "weight2",
    align: "center",
    width: 75,
  },
  {
    title: "主管3",
    dataIndex: "supervisorName3",
    key: "supervisorName3",
    align: "center",
    width: 100,
  },
  {
    title: "权重3",
    dataIndex: "weight3",
    key: "weight3",
    align: "center",
    width: 75,
  },
  {
    title: 'HRBP',
    dataIndex: 'hrName',
    key: 'hrName',
    align: 'center',
    width: 100,
  },
  {
    title: "相关人",
    dataIndex: "relatedPerson",
    key: "relatedPerson",
    align: "center",
    width: 400,
  },
  {
    title: '超级管理员',
    dataIndex: 'superAdminName',
    key: 'superAdminName',
    align: 'center',
    width: 120,
  },
  {
    title: '一级管理员',
    dataIndex: 'firstAdminName',
    key: 'firstAdminName',
    align: 'center',
    width: 120,
  },
  {
    title: '二级管理员',
    dataIndex: 'secondAdminName',
    key: 'secondAdminName',
    align: 'center',
    width: 120,
  },
  {
    title: "操作",
    key: "action",
    fixed: "right", // 固定在右侧
    align: "center",
    width: 160,
  },
]);

// 表格数据
const tableData = ref([]);
const scrollHeight = ref("60vh"); // 定义滚动区域高度
// 动态更新高度
const updateScrollHeight = () => {
  const screenHeight = window.innerHeight;
  const containerHeight = screenHeight - 50 - 10; // 假设父容器需要减去固定的偏移量
  if (screenHeight < 800) {
    scrollHeight.value = `${containerHeight * 0.62}px`;
  } else {
    scrollHeight.value = `${containerHeight * 0.7}px`;
  }
};
// 获取用户表
const getScoreMatrix = () => {
  getScoreMatrixApi(pagination.current, pagination.pageSize, serchInfo.name)
    .then((res) => {
      if (res.data.code === 200) {
        // 设置分页总数
        pagination.total = res.data.data.total;

        // 清空 data.value，避免多次追加导致重复数据
        tableData.value = [];

        // 获取数据
        for (let i = 0; i < res.data.data.data.length; i++) {
          tableData.value.push({
            index:
              pagination.current * pagination.pageSize -
              pagination.pageSize +
              i +
              1,
            userId: res.data.data.data[i].userId,
            name: res.data.data.data[i].name,
            supervisorName1: res.data.data.data[i].supervisorName1,
            supervisorName2: res.data.data.data[i].supervisorName2,
            supervisorName3: res.data.data.data[i].supervisorName3,
            weight1: res.data.data.data[i].weight1,
            weight2: res.data.data.data[i].weight2,
            weight3: res.data.data.data[i].weight3,
            department: res.data.data.data[i].department,
            lxyz: res.data.data.data[i].lxyz,
            businessType: res.data.data.data[i].business,
            firstAdminName: res.data.data.data[i].firstAdminName,
            secondAdminName: res.data.data.data[i].secondAdminName,
            superAdminName: res.data.data.data[i].superAdminName,
            hrName: res.data.data.data[i].hrName,
            relatedPerson: res.data.data.data[i].relatedPerson,
          });
        }
      }
    })
    .catch((err) => {
      message.error("请求失败！"+ err);
    });
};

// 处理翻页
const handleTableChange = (e) => {
  pagination.current = Number(e.current); //更新当前页码
  pagination.pageSize = e.pageSize; //更新每页显示条目数
  // sessionStorage.setItem('currentIndex', pagination.current)
  getScoreMatrix(); // 重新获取列表数据
};

const openDetail = ref(false); //查看详情弹窗默认不展示
const columnsDetail = ref([
  {
    title: "序号",
    dataIndex: "index",
    key: "index",
    width: "15%",
    align: "center",
  },
  {
    title: "用户名",
    dataIndex: "username",
    key: "username",
    // width: '15%',
    align: "center",
  },
  {
    title: "工号",
    dataIndex: "jobuNumber",
    key: "jobuNumber",
    // width: '30%',
    align: "center",
  },
  {
    title: "部门",
    dataIndex: "department",
    key: "department",
    // width: '30%',
    align: "center",
  },
  {
    title: "操作",
    key: "action",
    width: "15%",
    align: "center",
  },
]);
const dataDetail = ref([]);
const showDetail = (record) => {
  // 更新当前选择的被评估人id
  evaluatorId.value = record.userId;
  // 清空选择列表
  dataDetail.value = [];
  getRelatedPersonInfoApi(record.userId).then((res) => {
    if (res.data.code === 200) {
      for (let i = 0; i < res.data.data.length; i++) {
        dataDetail.value.push({
          index: i + 1,
          key: res.data.data[i].userId,
          username: res.data.data[i].name,
          jobuNumber: res.data.data[i].workNum,
          department: res.data.data[i].department,
        })
      }
    } else {
      message.error('获取失败！' + res.data.msg);
    }
  }).catch((err) => {
    console.log('请求失败！', err)
  });
  openDetail.value = true;
};
// 调用api-删除固定评估人
const delOne = (record) => {
  // 找到与record.key相等的项
  const index = dataDetail.value.findIndex((item) => item.key === record.key);
  // 调用api
  getDelEvaluatorApi(record.key, evaluatorId.value)
    .then((res) => {
      if (res.data.code === 200) {
        message.success("删除成功！");
        if (index !== -1) {
          dataDetail.value.splice(index, 1);
        }
        getScoreMatrix();
      } else {
        message.error("删除失败！" + res.data.msg);
      }
    })
    .catch((err) => {
      message.error("请求失败！"+ err);
    });
};
const openEvaluator = ref(false); //添加评测人弹窗默认不展示
const showEvaluator = () => {
  getUserList();
  openEvaluator.value = true;
};
const evaluator = ref([]);
const options = ref([]);
// 获取人员列表
const getUserList = () => {
  getUserListApi()
    .then((res) => {
      if (res.data.code === 200) {
        options.value = [
          {
            label: "空值",
            value: 0,
          },
        ];
        for (let i = 0; i <= res.data.data.length - 1; i++) {
          options.value.push({
            label:
              res.data.data[i].name + "(" + res.data.data[i].department + ")",
            value: res.data.data[i].userId,
            name: res.data.data[i].name,
          });
        }
      } else {
        message.error("获取失败！" + res.data.msg);
      }
    })
    .catch((err) => {
      message.error("请求失败！"+ err);
    });
};
// 调用api-添加固定评估人
const handleEvaluatorOk = () => {
  // 1、获取评估人key值
  // 2、调用添加评测人接口
  const evaluatorIds = Object.values(evaluator.value)
  // console.log('选择的人',evaluatorIds, '被评估人id', evaluatorId.value)

  if (evaluatorIds.length === 0) {
    message.error('请选择评估人！')
    return
  }
  getAddEvaluatorApi(evaluatorIds, evaluatorId.value)
    .then((res) => {
      if (res.data.code === 200) {
        message.success("添加固定评估人成功！^_^");
        // 清空选择框
        evaluator.value = [];
        // 刷新列表
        getScoreMatrix();
        // 3、关闭弹窗
        openEvaluator.value = false;
        openDetail.value = false;
      } else {
        message.error(res.data.msg);
      }
    })
    .catch((err) => {
      console.log("请求失败！", err);
    });
};

// 引用文件输入框
const fileInput = ref(null);
// 加载状态，控制加载动画
const loading = ref(false);

// 触发文件选择框
const triggerFileInput = () => {
  fileInput.value.click();
};

// 导入矩阵关系
const handleFileChange = (event) => {
  const file = event.target.files[0];
  if (file) {
    // 开始上传时显示加载动画
    loading.value = true;

    // 调用上传接口
    getMatrixExcelApi(file)
      .then((res) => {
        // 请求成功后的操作
        loading.value = false; // 隐藏加载动画
        if (res.data.code === 200) {
          message.success("文件上传成功~ =• ֊ •=");
          // 刷新页面
          setTimeout(() => {
            location.reload();
          }, 500); // 延迟0.5秒刷新页面
        } else {
          // 上传失败，显示后端返回的错误信息
          openNotificationWithIcon('warn', res.data.msg || '文件上传失败，未知原因');
          getScoreMatrix();
          // 上传失败后，重置文件输入框，便于用户重新选择文件
          event.target.value = ''; // 重置文件选择框
        }
      })
      .catch((err) => {
        loading.value = false; // 隐藏加载动画
        console.log("请求失败！", err);
        // 上传失败后，重置文件输入框，便于用户重新选择文件
        event.target.value = ''; // 重置文件选择框
      });
  }
};

// 矩阵关系表上传失败时展示错误信息
const openNotificationWithIcon = (type, message) => {
  notification[type]({
    message: '矩阵关系导入情况',
    description: message,
    duration: null
  });
};

// 获取当前时间并格式化
const getCurrentDateTime = () => {
  const now = new Date();
  const year = now.getFullYear();
  const month = (now.getMonth() + 1).toString().padStart(2, "0"); // 月份是从0开始的
  const day = now.getDate().toString().padStart(2, "0");
  return `${year}-${month}-${day}`;
};

// 处理导出并下载 矩阵关系Excel 文件
const downloadExcel = () => {
  getMatrixRelationExcel()
    .then((res) => {
      // 判断响应是否为文件流
      if (res && res.data) {
        const blob = new Blob([res.data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
        const link = document.createElement('a');
        const url = URL.createObjectURL(blob);

        // 使用当前日期来动态生成文件名
        const fileName = `矩阵关系导出表格${getCurrentDateTime()}.xlsx`;

        link.href = url;
        link.download = fileName;  // 设置文件名
        link.click();  // 触发下载
        message.success("矩阵关系导出成功！");
      } else {
        message.error("导出失败！未知错误");
      }
    })
    .catch((err) => {
      console.error('请求失败：', err);
      message.error("请求失败！");
    });
};

// 发布矩阵关系
const getPushRelation = () => {
  // 开始上传时显示加载动画
  loading.value = true;
  getPushRelationApi()
    .then((res) => {
      if (res.data.code === 200) {
        message.success("您已发布本轮评估矩阵关系!");
        published.value = true;
        loading.value = false; // 隐藏加载动画
      } else {
        message.error("发布失败！" + res.data.msg);
        loading.value = false; // 隐藏加载动画
      }
    })
    .catch((err) => {
      message.error("请求失败！" + err);
      loading.value = false; // 隐藏加载动画
    });
};

// 自定义搜索逻辑，只根据姓名进行搜索
const filterOption = (input, option) => {
  if (!input) return true;
  return option.label.toLowerCase().includes(input.toLowerCase());
};

// 初始加载数据
onMounted(() => {
  getScoreMatrix();
  getStepTime();
  updateScrollHeight();
  window.addEventListener("resize", updateScrollHeight);
});

// 在组件卸载时移除事件监听
onUnmounted(() => {
  window.removeEventListener("resize", updateScrollHeight);
});
</script>

<template>
  <div class="score-matrix">
    <!-- 表格顶部区域 -->
    <div class="content-header">
      <!-- 搜索表单 -->
      <div class="search-form">
        <a-form style="display: flex; align-items: center">
          <a-form-item style="margin-right: 10px; width: 300px">
            <a-input placeholder="请输入被评估人姓名" v-model:value="serchInfo.name" :allowClear="true" />
          </a-form-item>
          <a-form-item>
            <a-button type="primary" @click.prevent="searchSubmit" class="seachBtn">搜索</a-button>
          </a-form-item>
        </a-form>
      </div>
      <!-- 右侧按钮 -->
      <div class="submit-Btn">
        <a-button type="primary" style="margin-right: 10px" @click="triggerFileInput">导入数据</a-button>
        <a-button type="primary" style="margin-right: 10px" @click="downloadExcel">导出模板</a-button>
        <a-popconfirm placement="bottom" title="确定要发布本轮矩阵关系吗？" ok-text="确定" cancel-text="取消"
          @confirm="getPushRelation()">
          <a-button v-if="!published" type="primary" style="background-color: #39ba06">
            发布
          </a-button>
        </a-popconfirm>
        <a-button v-if="published" type="primary" disabled> 已发布 </a-button>
        <!-- 隐藏的文件上传输入框 -->
        <input ref="fileInput" type="file" accept=".xls,.xlsx" style="display: none" @change="handleFileChange" />

        <!-- 显示加载动画 -->
        <a-spin :spinning="loading" class="loading-spin" size="large" />
      </div>
    </div>

    <!-- 表格内容区域 -->
    <div class="content-table">
      <!-- 表格 -->
      <a-table :columns="columns" :data-source="tableData" :rowKey="(record) => record.key"
        :scroll="{ y: scrollHeight }" :pagination="pagination" :bordered="true" @change="handleTableChange">
        <!-- 自定义渲染操作列 -->
        <template v-slot:bodyCell="{ text, record, column }">
          <!-- 检查当前列是否为操作列 -->
          <template v-if="column.key === 'action'">
            <a-button type="text" @click="showDetail(record)" style="color: #0014b7">修改固定评估人</a-button>
          </template>
          <!-- 对其他列使用默认渲染 -->
          <template v-else>
            {{ text }}
          </template>
        </template>
      </a-table>
      <!-- 查看详情弹窗 -->
      <a-modal v-model:open="openDetail" title="修改固定评估人" :footer="null">
        <a-table :columns="columnsDetail" :data-source="dataDetail" :bordered="true" :pagination="false"
          :scroll="{ y: '60vh' }">
          <template #bodyCell="{ column, record }">
            <!-- 设置操作栏 -->
            <template v-if="column.key === 'action'">
              <a-popconfirm title="确定要移除吗？" ok-text="确定" cancel-text="取消" @confirm="delOne(record)">
                <a style="color: #bd3124; font-weight: bold"> 移除 </a>
              </a-popconfirm>
            </template>
          </template>
        </a-table>
        <div class="actionBtn">
          <a-button type="primary" @click="showEvaluator">添加评估人</a-button>
        </div>
      </a-modal>
      <!-- 添加评估人弹窗 -->
      <a-modal v-model:open="openEvaluator" title="添加评估人" @ok="handleEvaluatorOk">
        <a-select v-model:value="evaluator" :options="options" mode="multiple" placeholder="请选择" style="width: 100%" :filter-option="filterOption">
        </a-select>
        <!-- <a-select
          v-model:value="evaluator"
          placeholder="请选择"
          style="width: 100%"
        >
          <a-select-option
            v-for="item in options"
            :key="item.key"
            :value="item.key"
            >{{ item.value }}</a-select-option
          >
        </a-select> -->
      </a-modal>
    </div>
  </div>
</template>

