.actionBtn {
  text-align: center;
  /* 使按钮水平居中 */
  margin-top: 10px;
  /* 添加一些间距，让布局更美观 */
}

.loading-spin {
  position: fixed;
  /* 使得a-spin脱离正常文档流 */
  top: 6%;
  /* 距离顶部6% */
  left: 50%;
  /* 距离左边50% */
  transform: translate(-50%, -50%);
  /* 使用transform将a-spin精确居中 */
